.banner,
.banner__logo,
.banner__text {
  height: 72px;
}

.banner__logo {
  background-image: url('data:image/png;base64,');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 0 17px;
  width: 90px;
  font-size: 0;
  float: left;
}

.banner__text {
  color: #90a2bd;
  font-size: 15px;
  float: left;
  margin: 0 30px;
  line-height: 72px;
}


