.searchbox__select {
  /* Reset appearance */
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
  border: 0;
  background: transparent;
  background-image: none;
  font-size: inherit;
  font-weight: 400;
  border-radius: 0;
  /* Styling */
  cursor: pointer;
  border-bottom: 1px dashed #111;
  color: #000;
  padding: 5px;
}

.searchbox__select:hover {
  background-color: #111;
  color: #fff;
}


